import React from "react";
import burgerLogo from "../../assets/burger-logo.png";
import classes from "./Logo.css";

const logo = props => (
  <div className={classes.Logo}>
    <img src={burgerLogo} alt="myBurguer"></img>
  </div>
);

export default logo;



